}

@-webkit-keyframes animate {
    from {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
      }
}

@keyframes animate {
    from {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
      }
}

.bubble .c-Small, .bube .b-small, .blu .d-small {
    position: absolute;
    border-radius: 50%;
}

.bubble .c-Small:nth-child(1), .bube .b-small:nth-child(1), .blu .d-small:nth-child(1) {
    inset: 10px;
    border-left: 15px solid #6d777c;
    -webkit-filter: blur(3px);
            filter: blur(3px);
}

.bubble .c-Small:nth-child(2), .bube .b-small:nth-child(2), .blu .d-small:nth-child(2) {
    inset: 10px;
    border-right: 15px solid #4f2d39;
    -webkit-filter: blur(3px);
            filter: blur(3px);
}

.bubble .c-Small:nth-child(3), .bube .b-small:nth-child(3), .blu .d-small:nth-child(3) {
    inset: 20px;
    border-top: 15px solid #434239;
    -webkit-filter: blur(3px);
            filter: blur(3px);
}

.bubble .c-Small:nth-child(4), .bube .b-small:nth-child(4), .blu .d-small:nth-child(4) {
    inset: 30px;
    border-left: 15px solid #615056;
    -webkit-filter: blur(2px);
            filter: blur(2px);
}

.bubble .c-Small:nth-child(5), .bube .b-small:nth-child(5), .blu .d-small:nth-child(5) {
    inset: 10px;
    border-bottom: 15px solid rgb(78, 71, 71);
    -webkit-filter: blur(4px);
            filter: blur(4px);
    -webkit-filter: blur(5px);
            filter: blur(5px);
    -webkit-transform: rotate(330deg);
            transform: rotate(330deg);

}

.bubble:nth-child(2) {
    position: relative;
    zoom: 0.45;
    left: -10px;
    top: -100px;
    -webkit-animation-delay: -4s;
            animation-delay: -4s;
}

.bubble:nth-child(3) {
    position: relative;
    zoom: 0.25;
    right: -80px;
    top: -300px;
    -webkit-animation-delay: -6s;
            animation-delay: -6s;
}

.bubble:nth-child(4) {
    position: relative;
    zoom: 0.35;
    left: -120px;
    bottom: -200px;
    -webkit-animation-delay: -3s;
            animation-delay: -3s;
}

.bubble:nth-child(5) {
    position: relative;
    zoom: 0.5;
    top: 0px;
    left: 200px;
    -webkit-animation-delay: -5s;
            animation-delay: -5s;
}
