.one {
    width: 2px;
    height: 44px;
    transition: 5s;
    margin: 0px -10px 0px 0px;
    animation: anime 3s alternate-reverse infinite;
    background: white;
}

.two {
    width: 2px;
    height: 44px;
    transition: 5s;
    margin: 0px 0px 0px -10px;
    animation: anime2 3s alternate-reverse infinite;
    background: white;
}
@-webkit-keyframes anime{
    0%, 50%{
        transition: 3s;
        -webkit-transform: translateY(-50px) translateX(70px) rotate(-50deg);
                transform: translateY(-50px) translateX(70px) rotate(-50deg);
        opacity: 0.4;
    }
    50%, 100%{
        transition: 5s;
        -webkit-transform: rotate(50deg);
                transform: rotate(50deg);
    }
}
@keyframes anime{
    0%, 50%{
        transition: 3s;
        -webkit-transform: translateY(-50px) translateX(70px) rotate(-50deg);
                transform: translateY(-50px) translateX(70px) rotate(-50deg);
        opacity: 0.4;
    }
    50%, 100%{
        transition: 5s;
        -webkit-transform: rotate(50deg);
                transform: rotate(50deg);
    }
}
@-webkit-keyframes anime2{
    0%, 50%{
        transition: 3s;
        -webkit-transform: translateY(-50px) translateX(-70px) rotate(50deg);
                transform: translateY(-50px) translateX(-70px) rotate(50deg);
        opacity: 0.4;
    }
    50%, 100%{
        transition: 5s;
        -webkit-transform: rotate(-50deg);
                transform: rotate(-50deg);
    }
}
@keyframes anime2{
    0%, 50%{
        transition: 3s;
        -webkit-transform: translateY(-50px) translateX(-70px) rotate(50deg);
                transform: translateY(-50px) translateX(-70px) rotate(50deg);
        opacity: 0.4;
    }
    50%, 100%{
        transition: 5s;
        -webkit-transform: rotate(-50deg);
                transform: rotate(-50deg);
    }
}

