.icon_all,
.search {
    display: flex;
    align-items: center;
}

.nav_bot ul {
    display: flex;
}

.nav_bot li, a{
    margin: 0px 5px;
    list-style: none;
    font-size: 20px;
    color: #eee;
    text-decoration: none;
    font-family: 'Taviraj', serif;
}
.icon_sin{
    font-size: 24px;
    color: white;
    margin-right: 12px;
}
.icon_sin:nth-child(2),
.icon_sin:nth-child(4){
    font-size: 30px;
}
.icon_sin:nth-child(4){
    margin-right: 22px;
}
.text_field{
    color: #eee;
}

.nav_bot ul li, a{
    cursor: pointer;
    padding: 5px 10px;
}
.nav_bot ul li::before,
.nav_bot ul li::after{
    content: '';
    display: block;
    width: 100%;
    height: 1px;
    background: linear-gradient(to right, #080931, rgb(255, 255, 255));
    -webkit-transform: translateX(-100px);
            transform: translateX(-100px);
    opacity: 0;
    /* box-shadow: 5px 5px 50px 5px white; */
    transition: 1s;
}
.nav_bot ul li::before{
    background: linear-gradient(to left, #080931, rgb(255, 255, 255));
    -webkit-transform: translateX(100px);
            transform: translateX(100px);
}
.nav_bot ul li:hover::after,
.nav_bot ul li:hover::before{
    transition: 1s;
    opacity: 1;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
}
